.wrapper {
  max-width: 1600px;
  margin: 0 auto;
  


}

.switchLang {
  display: flex;
  margin-top: 10px;
  justify-content: center;
}

img {
  height: 25px;
  width: 30px;
  border: 1px solid black;
  align-items: center;


}

.wrapper-nav {
  width: 100%;
}

.navbar {
  max-width: 1200px;
  margin: auto;

  h1 {
    margin: 0;
    padding: 0;
  }
}

.navbar-nav {
  font-size: 18px;
  .nav-item {
    padding-bottom: 0;
    margin-left: 15px;
  }
}


.section-description-prg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding: 10px;          
  background-color: white;
  border-radius: 15px;
}

.image-gallery {
  max-width: 900px !important;;
}

.image-gallery-slide img {
  height: 600px !important;
  margin: 0 auto;
}