@font-face {
    font-family: 'fontello';  
    src: url(./font/fontello.woff?39819979);  
    font-weight: normal;  
    font-style: normal;  
  }


.about-me {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 90%;
    margin-top: 70px;
    max-width: 1024px;   
}



.contact  > span:first-of-type {
    margin-top: 10px;
    font-weight: bold;
    font-size: 1.4em;
}

.contact > div:last-of-type {
    margin-top: 10px;
}

.menu-item {
    position: relative;
    display: flex;    
    flex-basis: 160px;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    margin: 10px;
    padding: 20px;
    font-size: 17px;    
    color: #232323;
    background: #fff;
    text-align: center;
    box-shadow: 0 2px 0 #e2e2e2;
    border-radius: 5px;
    border: 1px solid transparent;  
    align-items: center;
    cursor: pointer;
}

.menu-items {
    display: flex;   
    flex-direction: row;
    flex-wrap: wrap; 
}


.foto {    
    width: 250px;
    height: 250px;
    border-radius: 150px;    
    background-size: cover;
    border: none;
}

.contact {
    display: flex;
    flex-direction: column;
    flex-basis: 300px;
    align-items: center;
    width: 40%;
    margin-right: 10px;
}


.demo-icon
{
    display: inline-block;
    width: 1em;
    margin-right: .2em;
    margin-left: .2em;
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    font-size: 3em;     
    text-decoration: inherit;
    text-align: center;  
    font-variant: normal;
    text-transform: none; 
    line-height: 1em;
}

 .linked-icon
{
    display: inline-block;
    width: 1em;
    margin-right: .2em;
    margin-left: .2em;    
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    text-decoration: none; 
    text-decoration: inherit;
    text-align: center; 
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
}

.linked-icon-title
{
    display: inline-block;
    width: 1em;
    margin-right: .2em;
    margin-left: .2em;    
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    text-decoration: none; 
    text-decoration: inherit;
    text-align: center; 
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
}

.linked-icon:hover {
    color: #e40510;
}

.menu-item:hover {
    color: #e40510;
}

@media screen and (max-width: 950px) {
    img {
        width: 100px;
        height: 100px;
    }
}


a {
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

.about-me-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
}





