@font-face {
    font-family: 'fontello';  
    src: url(./font/fontello.woff?39819979);  
    font-weight: normal;  
    font-style: normal;  
  }



body {
    flex-direction: column;
    display: flex;
    align-items: center;
    margin: 0px;
    background: #ebebeb;    
}

.header-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 50px;   
  
}

.header-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 75%;    
    max-width: 1024px;
}

.header-items a {
    display: inline-block;
    margin: 0 0 0 5px;
    padding: 7px;
    text-decoration: none;
    color: black;
    background: white;
    border-radius: 5px;   
}

.header-icon{
    display: inline-block;    
    width: 1em;
    margin-right: .2em;
    margin-left: .2em;    
    font-style: normal;
    font-weight: normal;
    line-height: 1em;
    font-family: "fontello";
    text-align: center;
    text-transform: none;
    text-decoration: inherit;  
}

.header-nav-desktop {
    display: inline-block;
}

.header-nav-desktop a:hover {
    color: #e40510;  
    font-weight: bold;
}

.header-nav-desktop-mobile {
    display: none;
}


.linked-icon
{
    display: inline-block;
    width: 1em;
    margin-right: .2em;
    margin-left: .2em;    
    font-family: "fontello";
    font-style: normal;
    font-weight: normal;
    text-decoration: none; 
    text-decoration: inherit;
    text-align: center; 
    font-variant: normal;
    text-transform: none;
    line-height: 1em;
}

.linked-icon:hover {
    color: #e40510;
}

.header {
    position: fixed;
    top: 0;
    z-index: 1000;
    width: 100%;
    height: 50px;
    background: #25205e;    
}


@media screen and (max-width: 950px) {
    .header-items >a:first-child {
        display: none;
    }

    .header-nav-desktop {
        display: none;
    }
    
    .header-nav-desktop-mobile {
        display: inline-block;
    }
}


