
.education-me-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
}

.section-description-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sections {
    display: flex;
    flex-direction: column;
    width: 90%;  
    max-width: 1024px;
}



.titleClass {
    width: 100%;
    margin-bottom: 6px;
    border-bottom: 4px double #333;
    text-align: right;    
    line-height: 1.3;
    clear: both;
    margin-top: 10px;   
    padding-bottom: 7px; 
}


.section-description {
    display: flex;
    justify-content: space-between; 
    margin-top: 20px;
    padding: 10px;          
    background-color: white;
    border-radius: 15px;
}

.text-part {    
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.text-project-description {
    text-align: center;
    font-weight: bold;
    font-size: 1.1em;
    margin-bottom: 20px;
}


.text-part  > span:first-child {
    font-weight: bold;
}

.university-foto {
    border-radius: 100px;
    width: 150px;
    height: 150px; 
}
